import React, { useState } from "react";
import { NavLink } from "react-router-dom";
import Info from "./Info/Info";
import "./Home.css";
import Hobbies from "./Hobbies/Hobbies";
import BluetoothAudioIcon from "@mui/icons-material/BluetoothAudio";
import WifiIcon from "@mui/icons-material/Wifi";
import SignalCellularAltIcon from "@mui/icons-material/SignalCellularAlt";
import Battery5BarIcon from "@mui/icons-material/Battery5Bar";
import CameraAltIcon from "@mui/icons-material/CameraAlt";
import CallIcon from "@mui/icons-material/Call";
import FingerprintIcon from "@mui/icons-material/Fingerprint";
import LockIcon from "@mui/icons-material/Lock";
import LockOpenIcon from "@mui/icons-material/LockOpen";

export default function Home() {
  const [unLock, setUnlock] = useState(false);

  return (
    <div className="home">
      <div className="firstContent">
        <div className="instructions">
          <p>
            Extrmement motiv pour dvelopper constamment mes comptences, je
            suis  la recherche d'un poste de{" "}
            <strong>dveloppeur front-end</strong>.
          </p>
          <br />
          <p>
            Je me spcialise dans l'<strong>intgration</strong> et le{" "}
            <strong>design de contenu</strong> mais je suis ouvert  tout type
            de projet me permettant d'voluer <strong> long terme</strong> au
            sein d'une entreprise.
          </p>
          <br />
          <p>
            J'ai le matriel et les qualits pour{" "}
            <strong>tltravailler</strong> efficacement.
          </p>
        </div>
        <div className="content">
          <Info />
        </div>
      </div>
      <div className="content">
        <div
          onMouseEnter={() => setUnlock(true)}
          onMouseLeave={() => setUnlock(false)}
          className="phone"
        >
          <div className="phone-icons-top">
            <BluetoothAudioIcon fontSize="smaller" />
            <WifiIcon fontSize="smaller" />
            <SignalCellularAltIcon fontSize="smaller" />
            <Battery5BarIcon fontSize="smaller" />
          </div>
          <div className="lock">{unLock ? <LockOpenIcon /> : <LockIcon />}</div>
          <Hobbies />
          <div className="phone-icons-bottom">
            <CallIcon fontSize="small" />
            <span className="fingerPrint">
              <FingerprintIcon fontSize="large" />
            </span>
            <CameraAltIcon fontSize="small" />
          </div>
        </div>
      </div>
        <p className="continu">Continuez votre visite vers <NavLink to="/experience" className="navContinu">mes expriences</NavLink></p>
    </div>
  );
}
