import React from 'react'
import { NavLink, Outlet } from 'react-router-dom'
import './Skills.css'
import SchoolIcon from '@mui/icons-material/School';

export default function Skills() {
  return (
    <div className='skillsContent'>
      <div className="diplomes">
        <div className="diplome">
          <SchoolIcon />
          <h4 className="diplomeTitle">Diplme d'tudes secondaires</h4>
          <p className='diplomeText'>Filire scientifique</p>
          <p className='diplomeText'>2008</p>
        </div>
        <div className="diplome">
          <SchoolIcon />
          <h4 className="diplomeTitle">Dveloppeur, Intgrateur web</h4>
          <p className='diplomeText'>RNCP 36076, Bac + 2</p>
          <p className='diplomeText'>2021-2022</p>
        </div>
        <div className="diplome">
          <SchoolIcon />
          <h4 className="diplomeTitle">Certificat de formation</h4>
          <p className='diplomeText'>React - Redux</p>
          <p className='diplomeText'>2022</p>
        </div>
      </div>
      <nav className='skillsNav'>
        <NavLink to='/skills/hardskills'
        className={({isActive}) => {
          console.log(isActive);
          return isActive ? "activeSkills" : "unActiveSkills"
        }}
        >Hard Skills</NavLink>
        <NavLink to='/skills/softskills'
        className={({isActive}) => {
          console.log(isActive);
          return isActive ? "activeSkills" : "unActiveSkills"
        }}
        >Soft Skills</NavLink>
      </nav>
      <Outlet />
      <p>Si mon profil vous intresse, vous pouvez rcuprer mes coordonnes <NavLink to="/contact" className="navContinu">ici</NavLink></p>
    </div>
  )
}
